import * as React from "react"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { cn } from "@/lib/utils"

export interface FloatingLabelInputProps
    extends React.InputHTMLAttributes<HTMLInputElement> {
    label: string
    icon?: React.ReactNode
}

const FloatingLabelInput = React.forwardRef<HTMLInputElement, FloatingLabelInputProps>(
    ({ className, label, icon, ...props }, ref) => {
        const [isFocused, setIsFocused] = React.useState(false)
        const [hasValue, setHasValue] = React.useState(false)

        const handleFocus = (e: React.FocusEvent<HTMLInputElement>) => {
            setIsFocused(true)
            props.onFocus?.(e)
        }

        const handleBlur = (e: React.FocusEvent<HTMLInputElement>) => {
            setIsFocused(false)
            setHasValue(e.target.value !== "")
            props.onBlur?.(e)
        }

        return (
            <div className="relative group">
                {/* Animated background gradient on focus */}
                <div className={cn(
                    "absolute inset-0 rounded-md bg-gradient-to-r from-primary/10 via-primary/5 to-transparent opacity-0 transition-opacity duration-300 -z-10",
                    isFocused && "opacity-100"
                )} />
                
                <Input
                    className={cn(
                        "peer pt-6 pb-1 h-14 border-2 transition-all duration-300",
                        icon && "pr-10",
                        isFocused && "border-primary ring-2 ring-primary/20",
                        !isFocused && "hover:border-primary/50",
                        className
                    )}
                    placeholder=" "
                    ref={ref}
                    onFocus={handleFocus}
                    onBlur={handleBlur}
                    {...props}
                />
                <Label
                    className={cn(
                        "absolute left-3 text-muted-foreground transition-all duration-300 ease-out pointer-events-none origin-[0] font-medium",
                        isFocused || hasValue
                            ? "top-1 text-xs text-primary font-semibold"
                            : "top-4 text-base",
                        "peer-[:not(:placeholder-shown)]:top-1 peer-[:not(:placeholder-shown)]:text-xs peer-[:not(:placeholder-shown)]:text-primary peer-[:not(:placeholder-shown)]:font-semibold"
                    )}
                >
                    {label}
                </Label>
                {icon && (
                    <div className={cn(
                        "absolute right-3 top-1/2 -translate-y-1/2 pointer-events-none transition-all duration-300",
                        isFocused ? "text-primary scale-110" : "text-muted-foreground"
                    )}>
                        {icon}
                    </div>
                )}
            </div>
        )
    }
)
FloatingLabelInput.displayName = "FloatingLabelInput"

export { FloatingLabelInput }
